import global from '../../common/global.js';
let apiUrl = global.api;
import request from '../../utils/request.js';
import {
	mapState,
	mapMutations
} from 'vuex';
export default {
	data() {
		return {
			user: "",
			pass: ""
		}
	},
	methods: {
		...mapMutations(['upId', 'expireTime', 'upToken', 'expireIn', 'tokenType']),
		// 登录
		is_login() {
			if (this.user === "" || this.pass === "") {
				uni.showToast({
					title: '请输入账户与密码',
					icon: "none"
				});
				return
			}
			uni.showLoading({
				title: '正在加载...',
			});
			const that = this;
			uni.request({
				url: apiUrl + '/api/app/login',
				method: 'POST',
				data: {
					mobile: this.user,
					password: this.pass
				},
				success(res) {
					if (res.statusCode == 200) {
						let user = res.data.user;
						let time = Date.parse(new Date()) / 1000;
						uni.setStorageSync("token", res.data.access_token);
						uni.setStorageSync("token_type", res.data.token_type);
						uni.setStorageSync("expires_in", res.data.expires_in);
						uni.setStorageSync("time", time);
						// that.expireTime(res.data.expires_in);
						// that.upToken(res.data.access_token);
						// that.expireIn(time);
						// that.tokenType(res.data.token_type);
						request("/api/auth/me", "POST").then(res => {
							uni.setStorageSync("user", JSON.stringify(res.data));
							let id = res.data.id;
							that.upId(id);
							uni.hideLoading();
							switch (res.data.type) {
								case 1:
									uni.redirectTo({
										url: '/pages/sweep/sweep',
									})
									break;
								case 2:
									uni.redirectTo({
										url: '/pages/List/list',
									})
									break;
								case 3:
									uni.redirectTo({
										url: '/pages/sweep/sweep',
									})
									break;
								default:
									break;
							}
						}).catch(res => {
							uni.showToast({
								title: '获取用户信息失败',
								icon: "none"
							})
						})
					} else {
						uni.hideLoading()
						uni.showToast({
							title: '账号或密码输入错误',
							icon: "none"
						})
					}
				}
			});
		},
		// 获取用户名
		change_user(e) {
			this.user = e.detail.value
		},
		// 获取密码
		change_pass(e) {
			this.pass = e.detail.value
		}
	}
}
